import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { VenueComponent} from "../venue/venue.component";
import { CeremonyComponent} from "../ceremony/ceremony.component";
import { TravelComponent } from "../travel/travel.component";
import { MenuComponent } from "../menu/menu.component";


const routes: Routes = [
  {path: 'practical-info', component: VenueComponent},
  {path: 'ceremony', component: CeremonyComponent},
  {path: 'venue', component: VenueComponent},
  {path: 'journey', component: VenueComponent},
  {path: 'photos', component: VenueComponent},
  {path: 'travel', component: TravelComponent},
  {path: 'menu', component: MenuComponent},
  {path: 'rsvp', component: VenueComponent},
  {path: 'accomodation', component: VenueComponent},

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



